header {
  width: 100%;
  height: 70px;
  margin: 0 auto;
}

header .hd_wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 32px;
  position: fixed;
  width: 100%;
  height: 70px;
  z-index: 10;
  padding: 0 200px;
  background: #fff;
}

header .logo a {
  display: flex;
  align-items: center;
  text-indent: -9999px;
  cursor: pointer;
}

header .btn_login a {
  display: block;
  width: 128px;
  height: 48px;
  color: var(--color-white);
  border-radius: 8px;
  background-color: var(--color-theme);
  font-size: 16px;
  font-weight: 600;
  text-align: center;
  line-height: 48px;
  cursor: pointer;
  transition: all 0.3s ease;
}

header .btn_login:hover a {
  background-color: var(--color-theme-hover);
}
nav {
  flex: 1;
}
nav .category-menu {
  display: flex;
}
nav .category-menu > li > a {
  display: block;
  padding: 0 15px;
  color: #4b5563;
  font-size: 18px;
  font-weight: 700;
  line-height: 70px;
}
nav .category-menu > li[data-cate="items"] a {
  color: #3692ff;
}
main .banner {
  width: 100%;
  background-color: var(--color-bg-skyblue);
}
main .banner .bnr_inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1110px;
  margin: 0 auto;
  padding-top: 200px;
}

main .banner .banner_txt > p {
  color: var(--color-gray-1);
  font-size: 40px;
  font-weight: 700;
  line-height: 56px;
}

main .banner .banner_txt .btn_visual {
  margin-top: 32px;
  text-align: center;
}

main .banner .banner_txt .btn_visual > a {
  display: block;
  width: 357px;
  height: 56px;
  border-radius: 40px;
  background-color: var(--color-theme);
  color: var(--color-white);
  font-size: 20px;
  font-weight: 600;
  line-height: 56px;
  cursor: pointer;
}

main .banner .img {
  font-size: 0;
}
main .banner .img img {
  width: 100%;
  max-width: 746px;
}

main .box {
  display: flex;
  align-items: center;
  gap: 64px;
  padding: 138px 0;
}

main .box.right {
  justify-content: flex-end;
}

main .box .img {
  max-width: 588px;
}

main .box .img img {
  width: 100%;
  height: auto;
}

main .box .info .badge {
  color: var(--color-theme);
  font-size: 18px;
  font-weight: 700;
}

main .box .info .title {
  padding-top: 12px;
  font-size: 40px;
  font-weight: 700;
  line-height: 56px;
}

main .box .info .box_txt {
  padding-top: 24px;
  font-size: 24px;
  font-weight: 500;
  line-height: 28.8px;
  letter-spacing: 0.08em;
}

footer .ft_inner {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  height: 160px;
  padding: 32px 200px;
  background-color: var(--color-bg-footer);
}

footer p {
  color: var(--color-gray-2);
  font-size: 16px;
}

footer ul {
  display: flex;
  align-items: center;
  gap: 30px;
}

footer ul.sns {
  gap: 12px;
}

footer ul li a {
  color: var(--color-white-2);
  font-size: 16px;
  font-weight: 400;
}

/* -- tb css -- */
@media (max-width: 1200px) {
  header .hd_wrap {
    padding: 0 22px;
  }

  main .banner .bnr_inner {
    display: block;
    position: relative;
    padding: 0;
    text-align: center;
  }
  main .banner .banner_txt {
    padding: 84px 0 210px;
    text-align: center;
  }
  main .banner .banner_txt.btm_bnr {
    margin-top: 56px;
    padding-top: 200px;
  }
  main .banner .banner_txt .btn_visual {
    display: inline-block;
    margin: 24px auto 0 auto;
  }
  main .banner .img img {
    max-width: 100%;
  }
  main .box {
    flex-direction: column;
    align-items: flex-start;
    gap: 24px;
    padding: 0;
  }
  main .box + .box {
    padding-top: 52px;
  }
  main .box.right {
    flex-direction: column-reverse;
    align-items: flex-end;
    text-align: right;
  }
  main .box .img {
    max-width: 100%;
    width: 100%;
  }
  main .box .info .title > br {
    display: none;
  }
  footer .ft_inner {
    padding: 32px 104px;
  }
}

/* -- mo css -- */
@media (max-width: 768px) {
  header .hd_wrap {
    gap: 8px;
    padding: 0 16px;
  }
  header .logo a {
    color: var(--color-theme);
    font-weight: 700;
    font-size: 25.63px;
    text-indent: 0px;
  }
  header .logo img {
    display: none;
  }
  nav .category-menu > li > a {
    padding: 0 8px 0 0;
    font-size: 16px;
    line-height: normal;
  }
  header .btn_login a {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: url("../images/user.png") no-repeat center/40px auto;
    text-indent: -9999px;
  }
  main .banner .banner_txt {
    padding: 48px 0 132px;
  }
  main .banner .banner_txt.btm_bnr {
    margin-top: 83px;
    padding: 120px 0 132px;
  }
  main .banner .banner_txt .btn_visual > a {
    width: 240px;
    height: 48px;
    line-height: 48px;
    font-size: 1.125rem;
  }
  main .banner .banner_txt > p {
    font-size: 2rem;
    line-height: 44.8px;
  }
  main .box {
    padding-top: 40px;
  }
  main .box:first-child {
    padding-top: 52px;
  }
  main .box .info .badge {
    font-size: 1rem;
  }
  main .box .info .title {
    font-size: 1.5rem;
    line-height: 32px;
  }
  main .box .info .box_txt {
    padding-top: 16px;
    font-size: 1rem;
    line-height: 19.2px;
  }
  footer .ft_inner {
    flex-wrap: wrap;
    position: relative;
    height: 100%;
    padding: 8.53%;
    gap: 60px 0;
  }
  footer .order-3 {
    order: 3;
    flex: 0 0 100%;
  }
}
